.debtorsList {
  background: var(--surface-primary);
  border-radius: var(--radius-lg);
  border: 1px solid var(--border-primary);
  height: 100%;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  transition: background-color 0.3s ease, border-color 0.3s ease;
}

.header {
  margin-bottom: 20px;
  flex-shrink: 0;
}

.title {
  font-family: var(--font-display);
  font-size: 1.5rem;
  font-weight: 400;
  color: var(--text-primary);
}

.tableContainer {
  flex: 1;
  overflow-y: auto;
  overflow-x: auto;
  min-height: 0;
}

.clientLink {
  color: var(--azure);
  text-decoration: none;
  font-weight: 500;
  transition: color 0.2s;
}

.clientLink:hover {
  color: var(--azure-dark);
  text-decoration: underline;
}

.groupBadge {
  display: inline-block;
  padding: 4px 12px;
  background: var(--gray-100);
  border-radius: var(--radius-sm);
  font-size: 0.8rem;
  font-weight: 500;
  color: var(--gray-700);
}

.amount {
  font-weight: 600;
  color: var(--text-primary);
}

.amountOverdue {
  font-weight: 600;
  color: var(--danger);
}

.daysLate {
  color: var(--danger);
  font-weight: 500;
}

.daysOnTime {
  color: var(--success);
  font-weight: 500;
}

.notifyBtn {
  padding: 6px 16px;
  border: 1px solid var(--azure);
  background: var(--surface-primary);
  color: var(--azure);
  border-radius: var(--radius-sm);
  font-size: 0.85rem;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s;
}

.notifyBtn:hover {
  background: var(--azure);
  color: var(--white);
}

